import Reward from 'components/home/Reward';
import LinkMenu from './LinkMenu';
import Carousel from 'components/Carousel.vue';
import ProductService from 'network/ProductService';
export default {
  data() {
    return {
      banners: []
    };
  },

  components: {
    Reward,
    LinkMenu,
    Carousel
  },

  created() {
    ProductService.getBanner().then(res => {
      let arr = [];
      let bannerObj = {
        url: res.data[0].url,
        img: res.data[0].img
      };

      for (let i = 0; i < 5; i++) {
        arr.push(bannerObj);
      }

      this.banners = arr;
    });
  }

};