export default {
  mounted() {
    let canvas = document.querySelector('#canvas');
    let c = canvas.getContext('2d'); // 为了使画的图不模糊

    let dpr = window.devicePixelRatio;
    canvas.width = 176 * dpr;
    canvas.height = 176 * dpr;
    c.scale(dpr, dpr); // 画灰色的线

    c.beginPath();
    c.lineWidth = 8;
    c.strokeStyle = 'rgba(168, 168, 168, 0.46)';
    c.setLineDash([1, 5]);
    c.arc(88, 88, 84, 0, 2 * Math.PI);
    c.stroke();
    c.closePath(); // 画红色的线

    c.beginPath();
    c.lineWidth = 8;
    c.strokeStyle = '#D42B2A';
    c.setLineDash([2, 4]);
    c.lineDashOffset = 0;
    c.arc(88, 88, 84, 0.5 * Math.PI, 1.75 * Math.PI);
    c.stroke();
    c.closePath();
  }

};